<template>
    <el-dialog class="add-dialog" :title="title" :visible.sync="dialogVisible" fullscreen @close="resetForm">
        <el-form ref="dialogForm" :model="dialogForm" label-width="180px" :rules="rules">
            <el-row :gutter="15">
                <el-col :span="8">
                    <el-form-item label="名称" prop="name">
                        <el-input v-model="dialogForm.name" placeholder="请填写名称" maxlength="100"></el-input>
                    </el-form-item>                                        
                    <el-form-item label="隶属单位详址" prop="subPlaceDetail">
                        <el-input v-model="dialogForm.subPlaceDetail" placeholder="请填写隶属单位详址" maxlength="200"></el-input>
                    </el-form-item>
                    <el-form-item label="隶属单位负责人联系方式" prop="subLeaderContact">
                        <el-input v-model="dialogForm.subLeaderContact" placeholder="请填写隶属单位负责人联系方式" maxlength="50"></el-input>
                    </el-form-item>
                    <el-form-item label="管辖单位联系方式" prop="domContact">
                        <el-input v-model="dialogForm.domContact" placeholder="请填写管辖单位联系方式" maxlength="50"></el-input>
                    </el-form-item>                    
                </el-col>
                <el-col :span="8">
                    <el-form-item label="线路类型" prop="lineType">
                        <el-select placeholder="请选择线路类型" v-model="dialogForm.lineType">
                            <el-option v-for="(item, index) in dic['线路类型']" :key="index" :label="item.name" :value="item.code"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="隶属单位联系方式" prop="subContact">
                        <el-input v-model="dialogForm.subContact" placeholder="请填写隶属单位联系方式" maxlength="50"></el-input>
                    </el-form-item>
                    <el-form-item label="分管治保负责人姓名" prop="securityPrincipalName">
                        <el-input v-model="dialogForm.securityPrincipalName" placeholder="请填写分管治保负责人姓名" maxlength="50"></el-input>
                    </el-form-item>
                    <el-form-item label="管辖单位名称" prop="domName">
                        <el-input v-model="dialogForm.domName" placeholder="请填写管辖单位名称" maxlength="100"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="隶属单位名称" prop="subName">
                        <el-input v-model="dialogForm.subName" placeholder="请填写隶属单位名称" maxlength="100"></el-input>
                    </el-form-item>
                    <el-form-item label="隶属单位负责人姓名" prop="subLeader">
                        <el-input v-model="dialogForm.subLeader" placeholder="请填写隶属单位负责人姓名" maxlength="50"></el-input>
                    </el-form-item>                                                           
                    <el-form-item label="分管治保负责人联系方式" prop="securityContact">
                        <el-input v-model="dialogForm.securityContact" placeholder="请填写分管治保负责人联系方式" maxlength="50"></el-input>
                    </el-form-item>
                    <el-form-item label="管辖单位详址" prop="domPlaceDetail">
                        <el-input v-model="dialogForm.domPlaceDetail" placeholder="请填写管辖单位详址" maxlength="200"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="15">
                <el-col :span="8">
                    <el-form-item label="治安隐患等级" prop="dangerLevel">
                        <el-select placeholder="请选择治安隐患等级" v-model="dialogForm.dangerLevel">
                            <el-option v-for="(item, index) in dic['治安隐患等级']" :key="index" :label="item.name" :value="item.code"></el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
                <el-col :span="16">
                    <el-form-item label="治安隐患情况">
                        <el-input v-model="dialogForm.dangerDesc" type="textarea" :rows="5" placeholder="请填写治安隐患情况"
                            maxlength="4000"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button type="warning" size="small" @click="resetForm">取消</el-button>
            <el-button type="success" size="small" @click="submitForm">保存</el-button>
        </div>
    </el-dialog>
</template>
<script>
import service from "./services/roadline_service.js";
export default {
  data() {
    return {
      dialogVisible: false,
      isAdd: true,
      title: "",
      dic: {
        线路类型: [],
        治安隐患等级: []
      },
      disCodeOption: [],
      dialogForm: {
        districtCode: ""
      },
      rules: {
        name: [
          {
            required: true,
            message: "请输入线路名称",
            trigger: "blur"
          }
        ],
        lineType: [
          {
            required: true,
            message: "请选择线路类型",
            trigger: "change"
          }
        ],
        subName: [
          {
            required: true,
            message: "请输入隶属单位名称",
            trigger: "blur"
          }
        ],
        // subPlaceDetail: [
        //   {
        //     required: true,
        //     message: "请输入隶属单位详址",
        //     trigger: "blur"
        //   }
        // ],
        // subContact: [
        //   {
        //     required: true,
        //     message: "请输入隶属单位联系方式",
        //     trigger: "blur"
        //   }
        // ],
        // subLeader: [
        //   {
        //     required: true,
        //     message: "请输入隶属单位负责人姓名",
        //     trigger: "blur"
        //   }
        // ],
        // subLeaderContact: [
        //   {
        //     required: true,
        //     message: "请输入隶属单位负责人联系方式",
        //     trigger: "blur"
        //   }
        // ],
        // domName: [
        //   {
        //     required: true,
        //     message: "请输入管辖单位名称",
        //     trigger: "blur"
        //   }
        // ],
        // domPlaceDetail: [
        //   {
        //     required: true,
        //     message: "请输入管辖单位详址",
        //     trigger: "blur"
        //   }
        // ],
        // domContact: [
        //   {
        //     required: true,
        //     message: "请输入管辖单位联系方式",
        //     trigger: "blur"
        //   }
        // ],
        // securityPrincipalName: [
        //   {
        //     required: true,
        //     message: "请输入分管治保负责人姓名",
        //     trigger: "blur"
        //   }
        // ],
        // securityContact: [
        //   {
        //     required: true,
        //     message: "请输入分管治保负责人联系方式",
        //     trigger: "blur"
        //   }
        // ],
        // dangerLevel: [
        //   {
        //     required: true,
        //     message: "请选择治安隐患等级",
        //     trigger: "change"
        //   }
        // ]
      }
    };
  },

  created() {
    let that = this;
    /** 获取字典表目录 */
    service.requestDicCatalog(data => {
      let dicKeys = _.keys(that.dic);
      _.map(data, item => {
        if (_.indexOf(dicKeys, item.name) != -1) {
          /** 通过字典表目录ID查询字典表 */
          service.requestDicType(item.id, data => {
            that.dic[item.name] = data;
          });
        }
      });
    });
  },

  methods: {
    submitForm() {
      this.$refs["dialogForm"].validate(valid => {
        if (valid) {
          let param = _.cloneDeep(this.dialogForm);
          param.dicMap && delete param.dicMap;
          if (this.isAdd == true) {
            // 添加
            service.requestSaveLineProte(param, data => {
              this.$popup.showNotification({
                type: "success",
                message: "护路护线添加成功！"
              });
              this.resetForm();
              this.$parent.handleQuery();
            });
          } else if (this.isAdd == false) {
            // 修改
            service.requestUpdateLineProte(param, data => {
              this.$popup.showNotification({
                type: "success",
                message: "护路护线修改成功！"
              });
              this.resetForm();
              this.$parent.handleQuery();
            });
          }
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    resetForm() {
      this.$refs["dialogForm"].resetFields();
      this.$data.dialogVisible = false;
      this.isAdd = true;
      this.dialogForm = {
        districtCode: ""
      };
    }
  }
};
</script>
<style>
.add-dialog .el-date-editor.el-input {
  width: 100%;
}

.add-dialog .el-select {
  width: 100%;
}

.add-dialog .el-cascader {
  width: 100%;
}
</style>